.AllContentSection {
    height: 100%;
    width: 100%;
    position: relative;
    margin-top: -90vh;
    /* overflow-x: hidden; */
    /* overflow-y: scroll; */
}
.scroll-content {
    position: fixed;
    width: 91%;
}
.AllContentSection_block {
    width: 100%;
    /* height: 100%; */
    backdrop-filter: blur(10px);
    border-radius: 40px;
    margin-top: 80vh;
    transition: 2s ease;
}